.btn-switch-toggle {
	position: relative;
	width: 57px;
	height: 25px;
	border: 1px solid #ccc;
	border-radius: 12.5px;
	background-color: #fff;
	overflow: hidden;
}

.btn-switch-toggle:focus {
	outline: 0 !important;
	cursor: pointer;
}

.switch-round {
	position: absolute;
	left: 4px;
	top: 4px;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background-color: #f76252;
	box-sizing: border-box;
	transition: left .3s;
}

.switch-round::before {
	content: "휴대폰";
	position: absolute;
	right: -28px;
	font-weight: 700;
	font-size: 11px;
	line-height: 16px;
	color: #f76252;
}

.btn-switch-toggle.on .switch-round {
	left: 35px;
	background-color: #897fd7
}

.btn-switch-toggle.on .switch-round::after {
	content: "24개월";
	position: absolute;
	left: -28px;
	font-weight: 700;
	font-size: 11px;
	line-height: 16px;
	color: #897fd7;
}

.btn-switch-toggle.type02 .switch-round {
	background-color: #9e9e9e;
}

.btn-switch-toggle.type02 .switch-round::before {
	content: "OFF";
	color: #9e9e9e;
}

.btn-switch-toggle.on.type02 .switch-round {
	background-color: #178bcf
}

.btn-switch-toggle.on.type02 .switch-round::after {
	content: "ON";
	color: #178bcf;
}

